.product-image-container {
  width: 90vw;
}

.main-image img {
  width: 100%;
  display: block;
  border-radius: 0.25rem;
  object-fit: cover;
  height: 300px;
}

.image-list-container {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  margin-top: 1rem;
  gap: 0.5rem;
}

.image-list {
  width: 100%;
  display: block;
  border-radius: 0.25rem;
  object-fit: cover;
  height: 50px;
  cursor: pointer;
}

.border {
  border: 2px solid #ab7a5f;
  border-radius: 0.25rem;
}

@media (min-width: 576px) {
  .main-image img {
    height: 600px;
  }

  .image-list {
    height: 100px;
  }
}

@media (min-width: 992px) {
  .main-image img {
    height: 500px;
  }

  .image-list {
    height: 90px;
  }

  .product-image-container {
    width: 45vw;
  }
}
