.image_container h2 {
  font-size: 2rem;
  text-transform: capitalize;
  letter-spacing: 0.1rem;
  margin-bottom: 0.75rem;
  line-height: 1.25;
}

.stars {
  display: flex;
  align-items: center;
}

.stars p {
  margin-left: 0.5rem;
}

.stars span {
  color: rgb(255, 185, 0);
  font-size: 1rem;
  margin-right: 0.25rem;
}

.image_container h5 {
  font-size: 0.875rem;
  color: #ab7a5f;
  margin: 0;
}

.image_container p {
  line-height: 2;
  max-width: 45em;
  color: #324d67;
  margin-bottom: 1.25rem;
  text-transform: capitalize;
}

.info-wrap {
  max-width: 40rem;
}

.info-wrap p {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.info-wrap span {
  font-weight: 700;
  text-transform: capitalize;
}

.product-hr {
  border: none;
  border-top: 1px solid #bcccdc;
}

@media (min-width: 992px) {
  .image_container p {
    font-size: 0.9rem;
  }

  .info-wrap {
    max-width: 20rem;
  }
}
